import { h } from 'preact';
import styled from 'styled-components';

const Root = styled.div`
    padding: 20px;
    text-align: center;
    background-color: #fff;
    margin: 12px;
    border-radius: 2px;
    border-bottom: 1px solid #eee;
    border-right: 0.5px solid #eee;
`;

export default () => (
    <Root>
        <span>404，你来到了荒无人烟的世界！</span>
    </Root>
);


