import { h, Component } from 'preact';
import styled from 'styled-components';
import Qrc from 'qrcode.react';
import { connect } from 'react-redux';

import { Btn, Title, Logo, Copyright, Footer, Qrcode } from '../components/FooterBar';
import Icon from '../components/Icon';

import route from '../route';
import * as indexAction from '../redux/actions/index';

const LogoIcon = require('../assets/logo_168.png');
const WxQrcodeIcon = require('../assets/qrcode-wechat.jpg');

const Root = styled.div`
    background-color: #fff;
    padding: 25px 30px;
    user-select: none;
    height: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
`;

const Header = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
`;

const Summary = styled.div`
    color: #333;
    padding: 8px 0 8px;
    text-indent: 2em;
`;

const Close = styled.div`
    position: absolute;
    top: 20px;
    left: 15px;
`;

const Version = styled.div`
    font-size: 13px;
    text-align: center;
    color: #444;
    margin-top: 10px;
`;

export default connect(
    state => ({ state: state.index }),
)(class extends Component {
    componentDidMount() {
        indexAction.init();
    }
    render({ state }) {
        return (
            <Root className='gradient'>
                <Header>
                    <Logo src={LogoIcon} />
                    <Title>{state.appName}</Title>
                </Header>
                <Summary>
                    {state.appSummary}
                </Summary>

                <Version>最新版本：{state.appVersion}</Version>
                <Btn className='gradient-dark'>
                    <a href={state.appUrl}>下载 App，查看更多原创内容</a>
                </Btn>
                <Copyright>中山原创音乐协会 © {new Date().getFullYear()} 版权所有</Copyright>
                <Footer>
                    <Qrcode className='gradient-dark'>
                        <Qrc value={state.appUrl} size={80} />
                        <span>扫码下载 App</span>
                    </Qrcode>
                    <Qrcode className='gradient-dark'>
                        <img src={WxQrcodeIcon} style={{ width: 80, height: 80 }} />
                        <span>关注公众号</span>
                    </Qrcode>
                </Footer>
                <Close onClick={e => route.go(-1)}>
                    <Icon name='close' color='rgba(255, 255, 255, 0.85)' size={26} />
                </Close>
            </Root>
        );
    }
});


