import { commit } from '../util';
import * as api from '../../api';
import storage from '../../storage';

// 更新播放索引
export const updatePlayIndex = index => {
    commit('video', state => ({ index }));
};

export const init = () => {
    api.videoList().then(res => {
        if (!res) return;
        commit('video', state => ({ ...res }));
        storage.set('VIDEO-LIST', res.list);
    });
};

export const detail = (id, update) => {
    api.videoDetail(id).then(res => {
        if (!res) return;
        update({ ...res, status: 2 });
    });
};

// 加载更多
export const loadmore = () => {

};




