import { h } from 'preact';
import styled from 'styled-components';

import Icon from '../../Icon';

const Root = styled.div`
    background-color: #fff;
    display: flex;
    align-items: center;
    padding-right: 6px;
    transition: background-color 0.3s ease-in-out;
    height: 50px;

    &:active {
        background-color: rgba(1, 1, 1, 0.12);
    }
`;

const Image = styled.img`
    height: 50px;
    width: 50px;
    border-radius: 2px;
`;

const Title = styled.div`
    font-size: 15px;
    color: #222;
    font-weight: 500;
    margin: 3px 0 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

const Name = styled.div`
    color: #789;
    font-size: 13px;
    margin: 0 0 3px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

const Panel = styled.div`
    flex: 1;
    margin-left: 8px;
`;

export default ({ thumb, title, author, style, onClick }) => (
    <Root style={style} onClick={onClick}>
        <Image src={thumb} onDragStart={e => false} />
        <Panel>
            <Title>{title}</Title>
            <Name>{author}</Name>
        </Panel>
        <Icon name='play_circle_outline' color='#aaa' size={20} />
    </Root>
);



