import { h } from 'preact';
import styled from 'styled-components';

import Tag from './Tag';


const Root = styled.div`
    position: relative;
    height: 100px;
    width: 90%;
    margin: 12px auto;
    display: flex;
    background-color: #f4f4f4;
`;

const Thumb = styled.div`
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    height: 100px;
    width: 100px;
    border-radius: 3px 0 0 3px;
`;

const Title = styled.div`
    flex: 1;
`;

const Num = styled.div`
    font-size: 14px;
    color: #f45;
    font-weight: bold;
    text-align: right;
`;

const Right = styled.div`
    flex: 1;
    padding: 8px;
    display: flex;
    flex-direction: column;
`;

export default ({ data: { title, thumb, price }, onClick }) => (
    <Root onClick={onClick}>
        <Thumb src={thumb} />
        <Right>
            <Title>{title.length > 16 ?
                title.slice(0, 16) + '...' : title
            }</Title>
            <Num>¥ {price}</Num>
        </Right>
        <Tag>商品</Tag>
    </Root>
);


