import nprogress from 'nprogress';
import axios from 'axios';

axios.defaults.baseURL = 'http://api.crxxxd.com';

export const get = (url, query) => {
    nprogress.start();
    return axios.post(url, query).then(res => {
        nprogress.done();
        return res.data;
    }).catch(err => {
        console.error(err);
        nprogress.done();
    });
};



