import { h } from 'preact';
import styled from 'styled-components';

import Avatar from '../../Avatar';

const PlayIcon = require('../../../assets/play.svg');

const Root = styled.div`
    height: 200px;
    position: relative;
    text-shadow: 1px 2px 1px rgba(1, 1, 1, 0.12);
`;

const Video = styled.video`
    height: 100%;
    width: 100%;
    background-color: #333;
    text-align: center;
    object-fit: scale-down;
`;

const Float = styled.div`
    width: 100%;
    height: 100%;
    /* box-shadow: 0 0 80px rgba(1, 1, 1, 0.8) inset; */
    background: url("${p => p.image}") center center no-repeat;
    background-size: cover;
    background-color: #f4f4f4;
`;

const Play = styled.img`
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    background-color: rgba(1, 1, 1, 0.24);
    border-radius: 100%;
`;

export default ({ play, thumb, url, author, avatar, style, onClick, showUser = true }) => (
    <Root style={style}>
        {play ?
            <Video controls="controls" autoPlay preload={false}>
                <source src={url} type="video/mp4" />
                <span style={{ color: '#fff' }}>
                    您的浏览器不支持 h5 video 视频播放。
                </span>
            </Video> :
            <Float image={thumb} onClick={onClick}>
                <Play src={PlayIcon} />
                {showUser &&
                    <Avatar url={avatar} style={{
                        padding: 6,
                    }} margin={5}>
                        <span style={{ color: '#fff' }}>{author}</span>
                    </Avatar>
                }
            </Float>
        }
    </Root>
);


