import { h } from 'preact';
import styled from 'styled-components';

import Tag from './Tag';
import Box from '../music/Box';


const Root = styled.div`
    position: relative;
    width: 90%;
    margin: 12px auto;
`;

export default ({ data: { title, thumb, author }, onClick }) => (
    <Root onClick={onClick}>
        <Box
            thumb={thumb}
            title={title}
            author={author}
            style={{ width: '100%', backgroundColor: '#f4f4f4' }}
        />
        <Tag>音乐</Tag>
    </Root>
);


