import { h, Component } from 'preact';
import styled from 'styled-components';

import route from '../route';
import Icon from './Icon';

const Root = styled.div`
    height: ${p => p.theme.navBarHeight}px;
    display: flex;
    align-items: center;
    padding: 0;
    background-color: #fff;
    color: #333;
    /* border-bottom: 1px solid #eee; */
    box-shadow: 1px 1px 3px rgba(1, 1, 1, 0.10);
`;

const Title = styled.span`
    flex: 1;
    padding: 0 12px;
    padding-right: 50px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
`;

const Btn = styled.div`
    height: 100%;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:active {
        background-color: rgba(1, 1, 1, 0.08);
    }
`;

export default class NavigationBar extends Component {

    goTo = e => {
        const ht = window.history;
        if (ht.length === 1) {
            return route.index();
        }
        ht.go(-1);
    }

    render({ title }) {

        return (
            <Root className='gradient'>
                <Btn onClick={this.goTo}>
                    <Icon name='chevron_left' color='#333' size={28} />
                </Btn>
                <Title>{title}</Title>
            </Root>
        );
    }
}




