import { h, Component } from 'preact';
import styled from 'styled-components';
import Modal from 'react-modal-container';

const Root = styled.div`
    position: fixed;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    display: inline;
    padding: 8px 16px;
    border-radius: 20px;
    background-color: rgba(1, 1, 1, 0.55);
    color: #fff;
    text-shadow: 1px 2px 1px rgba(1, 1, 1, 0.12);
    font-size: 14px;
    box-shadow: 1px 2px 3px rgba(1, 1, 1, 0.24);
`;

// Toast
export default (msg, time = 1000 * 3) => {

    const M = Modal(class Popup extends Component {
        componentDidMount() {
            this._timer = setTimeout(() => {
                // modal 是自动注入的 props
                this.props.modal.close();
            }, time);
        }
        componentWillUnmount() {
            clearTimeout(this._timer);
        }
        render() {
            const { msg } = this.props;
            return (
                <Root>
                    {msg}
                </Root>
            );
        }
    });

    const m = new M({ msg }).open();
};


