import { h } from 'preact';
import styled from 'styled-components';
import { connect } from 'react-redux';

import route from '../route';
import * as musicAction from '../redux/actions/music';
import * as homeAction from '../redux/actions/home';

import ShallowEqualComponent from '../components/ShallowEqualComponent';
import { arrayChunk } from '../functions';
import AlbumBox from '../components/views/album/Box';
import MusicBox from '../components/views/music/Box';
import Ad from '../components/Ad';
import Topbar from '../components/TopBar';
import FooterBar from '../components/FooterBar';

const Root = styled.div`
    padding: 0;
`;

const Mark = styled.div`
    color: #333;
    padding: 10px 12px;
    background-color: #fff;
    text-align: center;
    font-weight: 500;
`;

const Panel = styled.div`
    background-color: #fff;
    min-height: 120px;
    margin: 12px;
    border-bottom: 1px solid #eee;
    border-right: 0.5px solid #eee;
    display: flex;
    flex-direction: column;
`;

export default connect(
    state => ({ state: state.home }),
)(class extends ShallowEqualComponent {
    componentDidMount() {
        homeAction.init();
    }
    render({ state }) {
        return (
            <Root>
                <Topbar />
                <Ad data={state.ad} />
                <Mark style={{ marginTop: 12 }}>推荐专辑</Mark>
                <Panel>
                    {arrayChunk(state.albumList, 3).map((v, i) => (
                        <div key={i} style={{ display: 'flex' }}>
                            {v.map((item, index) => (
                                <AlbumBox key={index}
                                    {...item}
                                    onClick={e => route.albumDetail(item.id)}
                                />
                            ))}
                        </div>
                    ))}
                </Panel>
                <Mark>推荐音乐</Mark>
                <Panel style={{ padding: 6 }}>
                    {state.musicList.map((item, index) => (
                        <MusicBox key={index}
                            {...item}
                            style={{ marginTop: index === 0 ? 0 : 8 }}
                            onClick={e => {
                                musicAction.updateSong(item);
                                route.musicDetail(item.id);
                            }}
                        />
                    ))}
                </Panel>

                <FooterBar />
            </Root>
        );
    }
});


