import { h } from 'preact';

import styled from 'styled-components';

const Icon = styled.i.attrs({
    className: 'material-icons',
})`
    font-size: ${p => p.size}px;
    color: ${p => p.color};
    margin: ${p => p.margin};
    padding: ${p => p.padding};
    user-select: none;
`;

export default ({ name, size = 24, color = '#678', margin = 0, padding = 0, ...rest }) => (
    <Icon size={size} color={color} margin={margin} padding={padding} {...rest}>
        {name}
    </Icon>
);






