import { h } from 'preact';
import styled from 'styled-components';

const UserInfo = styled.div`
    background-color: #fff;
    padding: 20px;
    display: flex;
    position: relative;
    /* text-shadow: 1px 2px 2px rgba(1, 1, 1, 0.08); */
`;

const Header = styled.div`
    background-color: #fff;
    padding: 16px;

    span {
        color: #999;
        font-size: 12px;
    }
`;

const Title = styled.div`
    font-size: 20px;
    font-weight: bold;
`;

const Summary = styled.div`
    padding: 12px;
    background-color: #fff;
    margin-top: 12px;
    color: #555;
`;

const Row = styled.div`
    display: flex;
    justify-content: space-between;
`;

const Num = styled.div`
    font-size: 17px;
    color: #f45;
    font-weight: bold;
    margin-bottom: 6px;
`;

const Author = styled.div`
    font-size: 16px;
    font-weight: bold;
    color: #222;
`;

const Avatar = styled.div`
    width: 60px;
    height: 60px;
    border-radius: 5px;
    margin-right: 12px;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    background-color: rgba(1, 1, 1, 0.08);
`;

const AuthorSummary = styled.div`
    color: #444;
    font-size: 14px;
`;

const Time = styled.div`
    color: #555;
    font-size: 13px;
`;

export default ({ avatar, author = '', summary = '', time = '', isPublish = true }) => (
    <UserInfo className='gradient'>
        <Avatar src={avatar} />
        <div style={{ flex: 1 }}>
            <Row>
                <Author>{author}</Author>
                <Time>
                    发{isPublish ? '布' : '表'}于 {time.replace(/\d{4}-(\d{2}-\d{2})\s.*/, '$1')}
                </Time>
            </Row>
            <AuthorSummary>{summary && summary.length > 30 ?
                summary.slice(0, 30) + '...' : summary
            }</AuthorSummary>
        </div>
    </UserInfo>
);


