import { h } from 'preact';
import styled from 'styled-components';

const W = () => Math.min(window.innerWidth, 640) / 2 - 12 * 1.5;

const Root = styled.div`
    height: auto;
    width: ${W()}px;
    margin-left: 12px;
    margin-top: 12px;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 1px 2px 3px rgba(1, 1, 1, 0.08);
    overflow: hidden;
    display: flex;
    flex-direction: column;
`;

const Thumb = styled.div`
    background: url("${p => p.src}") center top no-repeat;
    background-size: cover;
    width: 100%;
    height: ${W() * 0.8}px;
    box-shadow: 0 -10px 20px rgba(1, 1, 1, 0.2) inset;
`;

const Title = styled.div`
    font-size: 14px;
    font-weight: bold;
    color: #333;
    flex: 1;
`;

const Num = styled.div`
    font-size: 14px;
    color: #f45;
    font-weight: bold;
    flex: 1;
`;

const Count = styled.div`
    font-size: 12px;
    color: #999;
`;

const Row = styled.div`
    display: flex;
    align-items: center;
`;

const Info = styled.div`
    display: flex;
    flex-direction: column;
    padding: 6px;
    flex: 1;
`;

export default ({ thumb, title, price, payNum, onClick }) => (
    <Root onClick={onClick}>
        <Thumb src={thumb} />
        <Info>
            <Title>{title.length > 20 ?
                title.slice(0, 20) + '...' : title
            }</Title>
            <Row>
                <Num>¥ {price}</Num>
                <Count>{payNum} 人付款</Count>
            </Row>
        </Info>
    </Root>
);


