import { h } from 'preact';
import { ThemeProvider } from 'styled-components';
import { connect } from 'react-redux';

// 主题容器
export default connect(
    state => ({ state: state.theme }),
)(({ state, children }) => (
    <ThemeProvider theme={state}>
        {children}
    </ThemeProvider>
));




