import { h } from 'preact';
import styled from 'styled-components';

const W = () => Math.min(window.innerWidth, 640) / 3 - 8;

const Root = styled.div`
    width: calc(33.3%);
    background-color: #fff;
    display: flex;
    flex-direction: column;
    transition: background-color 0.3s ease-in-out;

    &:active {
        background-color: rgba(1, 1, 1, 0.12);
    }
`;

const Image = styled.div`
    height: ${W()}px;
    width: ${W()}px;
    pointer-events: none;
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    background-color: #fafafa;
`;

const Title = styled.div`
    font-size: 14px;
    font-weight: bold;
    color: #333;
    margin: 6px 6px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

const Name = styled.div`
    color: #789;
    font-size: 13px;
    margin: 0 6px 3px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
`;

export default ({ image, vcount, title, author, onClick }) => (
    <Root onClick={onClick}>
        <Image src={image} onDragStart={e => false} />
        <Title>{title}</Title>
        <Name>{author}</Name>
    </Root>
);


