import { get } from './http';
import toast from '../components/modal/toast';

const APIS = {
    activityList: '/yinyue/h5/getDynamicList',
    activityDetail: '/yinyue/h5/getDynamicDetail',

    musicList: '/yinyue/h5/getMusicList',
    musicDetail: '/yinyue/h5/getMusicDetail',

    videoList: '/yinyue/h5/getVideoList',
    videoDetail: '/yinyue/h5/getVideoDetail',

    goodsList: '/yinyue/h5/goodsList',
    goodsDetail: '/yinyue/h5/goodsDetail',

    albumList: '/yinyue/h5/getAlbumList',
    albumDetail: '/yinyue/h5/getAlbumDetail',

    ad: '/yinyue/h5/getAdvice',
    appInfo: '/yinyue/h5/getVersionInfo',
};

export const getPhotos = (photos) => {
    try {
        const res = Array.isArray(photos) ? photos : JSON.parse(photos);
        if (Array.isArray(res)) return res;
        else return [];
    }
    catch (err) {
        return [];
    }
};

function getTypeData(data) {
    const arr = [];
    if (!data.photo_urls) arr[0] = [];
    else {
        arr[0] = getPhotos(data.photo_urls);
    }

    if (!!data.relation_data && typeof data.relation_data === 'object') {
        // music
        arr[1] = {
            id: data.relation_data.id,
            title: data.relation_data.title || '',
            thumb: data.relation_data.thumb,
            author: data.relation_data.nickname,
        };
        // video
        arr[2] = {
            id: data.relation_data.id,
            title: data.relation_data.title || '',
            thumb: data.relation_data.thumb,
        };
        // goods
        arr[3] = {
            id: data.relation_data.id,
            title: data.relation_data.title || '',
            thumb: data.relation_data.thumb,
            price: data.relation_data.price,
        };
    }
    else {
        arr.push(...[null, null, null]);
    }

    return arr[data.auto_create - 1];
}

// 请求缓存
const Memo = {
    _C: {},
    get(key) {
        const d = this._C[key];
        if (d) {
            // 检查时间
            if (Date.now() - d.time > 1000 * 60 * 30) {
                // 过期
                this._C[key] = null;
            }
            else {
                return d.data;
            }
        }
        return null;
    },
    set(key, val) {
        this._C[key] = {
            time: Date.now(),
            data: val,
        };
    }
};

export const ad = () => {
    const m = Memo.get('ad');
    if (m) return Promise.resolve(m);

    return get(APIS.ad, { advice_id: 1 }).then(res => {
        if (res.code === 1) {
            Memo.set('ad', res.data);
            return res.data;
        }
        else {
            toast(res.msg);
        }
    });
};

export const appInfo = () => {
    return get(APIS.appInfo).then(res => {
        if (res.code === 1) {
            return {
                appVersion: res.data.version,
                appUrl: res.data.apk,
                appName: res.data.app_name,
            };
        }
        else {
            toast(res.msg);
        }
    });
};

// album
export const albumList = (page = 1, limit = 6) => {
    return get(APIS.albumList, { page, limit }).then(res => {
        if (res.code === 1) {
            return {
                total: res.data.total,
                current: res.data.current_page,
                last: res.data.last_page,
                list: res.data.data.map(i => ({
                    id: i.id,
                    image: i.thumb,
                    title: i.title,
                    author: i.nickname,
                })),
            };
        }
        else {
            toast(res.msg);
        }
    });
};

export const albumDetail = id => {
    const m = Memo.get('album-' + id);
    if (m) return Promise.resolve(m);

    return Promise.all([
        get(APIS.albumDetail, { album_id: id }),
        get(APIS.musicList, { album_id: id }),
    ]).then(([album, music]) => {
        if (album.code === 1 && music.code === 1) {
            const data = {
                thumb: album.data.thumb,
                title: album.data.title || '',
                summary: album.data.describe || '',
                author: album.data.nickname,
                avatar: album.data.avatar,
                list: music.data.data.map(i => ({
                    id: i.id,
                    title: i.title,
                    author: i.nickname,
                    url: i.url,
                    thumb: i.thumb,
                    avatar: i.avatar,
                })),
            };

            Memo.set('album-' + id, data);
            return data;
        }
        else {
            toast(album.msg);
        }
    });
};

// activity

export const activityList = (page = 1, limit = 10) => {
    return get(APIS.activityList, { page, limit }).then(res => {
        if (res.code === 1) {
            return {
                total: res.data.total,
                current: res.data.current_page,
                last: res.data.last_page,
                list: res.data.data.map(i => ({
                    id: i.id,
                    avatar: i.avatar,
                    author: i.nickname,
                    level: ['普通用户', '个人商家', '企业商家', '音乐人'][i.user_level - 1],
                    content: i.content || '',
                    summary: i.abstract || '',
                    time: i.create_time,
                    type: ['image', 'music', 'video', 'goods'][i.auto_create - 1],
                    typeData: getTypeData(i),
                }))
            };
        }
        else {
            toast(res.msg);
        }
    });
};

export const activityDetail = id => {
    const m = Memo.get('activity-' + id);
    if (m) return Promise.resolve(m);

    return get(APIS.activityDetail, { dynamic_id: id }).then(res => {
        if (res.code === 1) {
            const data = {
                id: res.data.id,
                avatar: res.data.avatar,
                author: res.data.nickname,
                level: ['普通用户', '个人商家', '企业商家', '音乐人'][res.data.user_level - 1],
                content: res.data.content || '',
                summary: res.data.abstract || '',
                time: res.data.create_time,
                type: ['image', 'music', 'video', 'goods'][res.data.auto_create - 1],
                typeData: getTypeData(res.data),
            };

            Memo.set('activity-' + id, data);
            return data;
        }
        else {
            toast(res.msg);
        }
    });
};

