import { h } from 'preact';
import styled from 'styled-components';

import VideoBox from './Box';


const Info = styled.div`
    padding: 10px 12px;
`;

const Title = styled.div`
    font-size: 17px;
    font-weight: bold;
    color: #222;
    flex: 1;
`;

const Summary = styled.div`
    color: #777;
    font-size: 14px;
`;

const Time = styled.div`
    color: #999;
    font-size: 13px;
`;

const Row = styled.div`
    display: flex;
    align-items: center;
`;

const Root = styled.div`
    background-color: #fff;
    transition: background-color 0.3s ease-in-out;

    &:active {
        background-color: #f4f4f4;
    }
`;

export default (props) => (
    <Root>
        <VideoBox {...props} onClick={e => props.onPlay && props.onPlay(e)} />
        <Info onClick={e => props.onInfo && props.onInfo(e)}>
            <Row>
                <Title>{props.title && props.title.length > 16 ?
                    props.title.slice(0, 16) : props.title
                }</Title>
                <Time>发表于 {props.time.replace(/\d{4}-(\d{2}-\d{2})\s.*/, '$1')}</Time>
            </Row>
            <Summary>{props.describe && props.describe.length > 50 ?
                props.describe.slice(0, 50) + '...' : props.describe
            }</Summary>
        </Info>
    </Root>
);


