import { commit } from '../util';
import * as api from '../../api';
import storage from '../../storage';
import toast from '../../components/modal/toast';

// 更新播放状态
export const updatePlayStatus = (play) => {
    commit('music', state => {
        if (typeof play === 'boolean') {
            return { play };
        }
        else {
            if (state.song.url.length <= 0 && !state.play === true) {
                toast('加载失败');
            }
            return { play: !state.play };
        }
    });
};

// 更新进度数据
let npdn = 0;
export const updateProgressData = (data) => {
    if (npdn % 3 === 0) { // 减低性能消耗
        commit('music', state => ({ progressData: data }));
        if (npdn > 100000) npdn = 0;
    }
};

// 更新当前音乐源
export const updateSong = (data) => {
    commit('music', state => ({ song: data }));
    storage.set('MUSIC-DATA', data);
};

// 打开详细页
export const detail = (id, update) => {
    api.musicDetail(id).then(res => {
        if (!res) return;
        commit('music', state => ({ song: res }));
        storage.set('MUSIC-DATA', res);
    });
};



