import { h, Component } from 'preact';


export default class MySwiper extends Component {

    static defaultProps = {
        options: {},
    }

    constructor(props) {
        super(props);
        this.swiper = null;
    }

    componentWillReceiveProps(nextProps) {
        if (this.swiper) {
            this.swiper.update();
        }
    }

    componentDidUpdate() {
        if (this.swiper) {
            this.swiper.update();
        }
    }

    componentDidMount() {
        this.swiper = new window.Swiper(this.swiperview, this.props.options);
    }

    componentWillUnmount() {
        this.swiper.destroy();
        this.swiper = null;
    }

    render({ children, style }) {

        return (
            <div className="swiper-container" ref={r => this.swiperview = r} style={style}>
                <div className="swiper-wrapper">
                    {Array.isArray(children) &&
                        children.map((item, index) => (
                            <div className="swiper-slide" key={index}>{item}</div>
                        ))
                    }
                </div>
                <div className="swiper-pagination" id={`pagination-${this.props.id}`}></div>
            </div>
        );
    }
}



