import { h, Component } from 'preact';
import styled from 'styled-components';

const Root = styled.div`
    position: fixed;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: 1001;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(1, 1, 1, 0.12);
    box-shadow: 0 0 300px rgba(1, 1, 1, 0.36) inset;
    flex-direction: column;

    ${p => !p.show && `
        animation: loading-run 0.4s ease-in-out;
        animation-fill-mode: forwards;
    `};

    ${p => p.close && `
        display: none;
    `}
`;

const Ant = styled.div`
    width: 60px;
    height: 60px;
    margin: 100px auto;
    background-color: #f34;

    border-radius: 100%;
    animation: scaleout 1s infinite ease-in-out;

    @keyframes scaleout {
        0% {
            transform: scale(0.0);
        } 100% {
            transform: scale(1.0);
            opacity: 0;
        }
    }
`;

export default class Loading extends Component {
    constructor(props) {
        super(props);
        this.state = {
            close: false
        };

        if (this.props.show) {
            this.state.close = false;
        }
        else {
            this.state.close = true;
        }
    }

    componentWillReceiveProps(nextProps) {
        // true -> false
        if (this.props.show && !nextProps.show) {
            setTimeout(() => {
                this.setState({ close: true });
            }, 400);
        }
        // if (!this.props.show && this.state.close) {
        //     this.setState({ close: false });
        // }
    }

    render({ show }) {
        const { close } = this.state;
        return (
            <Root show={show} close={close}>
                <Ant></Ant>
                <div style={{ textAlign: 'center' }}>加载中...</div>
            </Root>
        );
    }
}




