import { route } from 'preact-router';

function scrollTop() {
    setTimeout(() => {
        window.scrollTo(0, 0);
    }, 10);
}

export default {
    index: () => {
        route('/');
    },
    home: () => {
        route('/home/');
    },
    musicDetail: id => {
        route('/music-detail/' + id);
        // scrollTop();
    },
    videoDetail: id => {
        route('/video-detail/' + id);
        // scrollTop();
    },
    goodsDetail: id => {
        route('/goods-detail/' + id);
        // scrollTop();
    },
    activityDetail: id => {
        route('/activity-detail/' + id);
        // scrollTop();
    },
    albumDetail: id => {
        route('/album-detail/' + id);
        // scrollTop();
    },
    download: () => {
        route('/download/');
    },
    go: n => {
        const ht = window.history;
        if (ht.length === 1) {
            return route.index();
        }
        ht.go(n);
        // scrollTop();
    }
};



