import { h, Component } from 'preact';
import styled from 'styled-components';

const HomeIcon = require('../assets/home.svg');
const VideoIcon = require('../assets/video.svg');
const MallIcon = require('../assets/mall.svg');
const ActivityIcon = require('../assets/activity.svg');

const Root = styled.div`
    height: ${p => p.theme.tabsViewHeight}px;
    display: flex;
    align-items: center;
    padding: 0;
    background-color: #fff;
    box-shadow:
        1px -2px 5px rgba(1, 1, 1, 0.05),
        1px 2px 5px rgba(1, 1, 1, 0.05);
    justify-content: space-around;
    position: fixed;
    bottom: 0; left: 0; right: 0;
    max-width: ${p => p.theme.mainMaxWidth}px;
    margin: 0 auto;
    z-index: 100;
`;

const Icon = styled.img`
    height: ${64 * 0.35}px;
    width: ${64 * 0.35}px;
`;

const Tab = styled.div`
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: background-color 0.3s ease-in-out;
    color: rgba(1, 1, 1, 0.46);
    ${'' /* color: rgba(255, 255, 255, 0.6); */}

    > span {
        font-weight: bold;
        margin-top: 2px;
        font-size: 14px;
    }

    ${p => p.active && `
        background-color: rgba(1, 1, 1, 0.06);
        color: #333;
    `}

    &:active {
        background-color: rgba(1, 1, 1, 0.12);
    }
`;

const Mini = styled.div`
    height: 46px;
    width: 46px;
    border-radius: 100px;
    background-color: #f4f4f4;
    text-align: center;
    margin: 0 12px;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
`;

const Thumb = styled.div`
    height: 100%;
    width: 100%;
    border-radius: 100px;
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    animation: music-thumb 60s infinite linear;
    ${p => !p.play && `
        animation-play-state: paused;
    `}
`;

export default class TabsView extends Component {

    static defaultProps = {
        onTab: e => e,
        onMini: e => e,
        index: 0,
        image: '',
    }

    componentDidMount() {
        const root = document.getElementById('tabbar');
        document.addEventListener('scroll', e => {
            const s = {
                t: () => document.documentElement.scrollTop || document.body.scrollTop,
                H: () => document.documentElement.scrollHeight || document.body.scrollHeight,
                h: () => document.documentElement.clientHeight || document.body.clientHeight,
            };
            if (s.t() + s.h() + 240 >= s.H()) {
                console.log('到底了');
                root.classList.add('gradient');
            }
            else {
                root.classList.remove('gradient');
            }
        });
    }


    render({ onMini, onTab, index, image, play }) {
        return (
            <Root id='tabbar'>
                <Tab active={index === 0} onClick={e => onTab(0)}>
                    <Icon src={HomeIcon} style={{ width: 74 * 0.35 }} />
                    <span>首页</span>
                </Tab>
                <Tab active={index === 1} onClick={e => onTab(1)}>
                    <Icon src={ActivityIcon} />
                    <span>动态</span>
                </Tab>
                <Mini onClick={onMini}>
                    <Thumb src={image} onDragStart={e => false} play={play} />
                </Mini>
                <Tab active={index === 2} onClick={e => onTab(2)}>
                    <Icon src={VideoIcon} />
                    <span>视频</span>
                </Tab>
                <Tab active={index === 3} onClick={e => onTab(3)}>
                    <Icon src={MallIcon} />
                    <span>商城</span>
                </Tab>
            </Root>
        );
    }
}



