import { h } from 'preact';
import styled from 'styled-components';

import Box from './Box';
import Avatar from '../../Avatar';

const Root = styled.div`
    background-color: #fff;
    margin: 8px 0;
    padding: 12px;
    border-bottom: 0.5px solid #eee;
`;

const Author = styled.div`
    margin-left: 6px;
    font-weight: bold;
    flex: 1;

    .sum {
        font-size: 12px;
        color: #777;
        font-weight: normal;
    }
`;

const Row = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
`;

const Time = styled.div`
    font-size: 13px;
    color: #888;
    font-weight: normal;
`;

export default (props) => (
    <Root>
        <Avatar url={props.avatar}
            size={40} style={{ width: '100%' }}
            onClick={props.onClick}>
            <Author>
                <Row>
                    <div style={{ flex: 1 }}>{props.author}</div>
                    <Time>发表于 {props.time.replace(/\d{4}-(\d{2}-\d{2})\s.*/, '$1')}</Time>
                </Row>
                <div class='sum'>{props.summary && props.summary.length > 16 ?
                    props.summary.slice(0, 16) + '...' : props.summary
                }</div>
            </Author>
        </Avatar>
        <Box
            {...props}
        />
    </Root>
);


