import { h, Component } from 'preact';
import styled from 'styled-components';

import route from '../route';

import * as musicAction from '../redux/actions/music';
import * as homeAction from '../redux/actions/home';

import NavigationBar from '../components/NavigationBar';
import Avatar from '../components/Avatar';
import Loading from '../components/Loading';
import MusicBox from '../components/views/music/Box';
import EmptyBox from '../components/EmptyBox';

const Root = styled.div`
    padding-bottom: 10px;
`;

const Header = styled.div`
    background-size: cover;
    position: relative;
    ${'' /* height: 180px; */}
    padding: 30px 16px 36px;
    display: flex;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
    margin-bottom: 30px;
`;

const Thumb = styled.div`
    height: 120px;
    width: 120px;
    background: url("${p => p.image}") center center no-repeat;
    background-size: cover;
    background-color: rgba(1, 1, 1, 0.1);
    border-radius: 2px;
    box-shadow: 1px 2px 3px rgba(1, 1, 1, 0.12);
    margin-right: 15px;
`;

const Title = styled.div`
    color: #333;
    font-size: 18px;
    font-weight: bold;
`;

const Summary = styled.div`
    font-size: 14px;
    color: #484848;
    margin-top: 4px;
`;

const List = styled.div`
    background-color: #fff;
    /* min-height: 120px; */
    margin: 0 12px 12px;
    border-bottom: 1px solid #eee;
    border-right: 0.5px solid #eee;
    display: flex;
    flex-direction: column;
    padding: 6px;
`;

const Count = styled.div`
    padding: 10px 50px;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
    background-color: #f34;
    margin: 0 auto;
    display: inline-block;
    border-radius: 20px;
    color: #414141;
    transform: translateY(-25px);
    position: absolute;
    bottom: -45px;
    margin: 0 auto;
    left: 20%; right: 20%;
    text-align: center;
`;

export default class AlbumDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: this.props.id,
            thumb: '',
            title: '',
            author: '',
            avatar: '',
            summary: '',
            list: [],
            // 加载状态
            status: 1,
        };
    }

    componentDidMount() {
        homeAction.albumDetail(this.state.id, this.setState.bind(this));
    }

    render() {
        const { thumb, title, author, avatar, summary, list, status } = this.state;

        return (
            <Root>
                {/* <Loading show={status === 1} /> */}
                <NavigationBar title='专辑' />
                <Header className='gradient'>
                    <Thumb image={thumb} />
                    <div style={{ flex: 1 }}>
                        <Title>{title.length > 20 ? title.slice(0, 20) + '...' : title}</Title>
                        <Avatar url={avatar} size={22}>
                            <span style={{ marginLeft: 5, color: '#333' }}>
                                {author}
                            </span>
                        </Avatar>
                        <Summary>{summary.length > 40 ?
                            summary.slice(0, 40) + '...' : summary
                        }</Summary>
                    </div>
                    <Count className='gradient'>{list.length} 首歌</Count>
                </Header>

                <List>
                    {list.length === 0 && <EmptyBox />}
                    {list.map((item, index) => (
                        <MusicBox key={index}
                            {...item}
                            style={{ marginTop: index === 0 ? 0 : 8 }}
                            onClick={e => {
                                musicAction.updateSong(item);
                                setTimeout(() => route.musicDetail(item.id), 20);
                            }}
                        />
                    ))}
                </List>
            </Root>
        );
    }
}


