import { h } from 'preact';
import styled from 'styled-components';

const Avatar = styled.div`
    height: ${p => p.size}px;
    width: ${p => p.size}px;
    border-radius: 100%;
    margin: ${p => typeof p.margin === 'number' ?
        `${p.margin}px` : p.margin
    };

    background: url("${p => p.src}") center  center no-repeat;
    background-size: cover;
    background-color: rgba(1, 1, 1, 0.1);
`;

const Root = styled.div`
    display: flex;
    align-items: center;
`;

export default ({ url, size = 30, margin, children, style, ...rest }) => (
    <Root style={style} {...rest}>
        <Avatar
            src={url}
            onDragStart={e => false}
            size={size}
            margin={margin}
        />
        <div style={{ flex: 1 }}>
            {children}
        </div>
    </Root>
);


