
// 数组切块 [1, 2, 3, 4, 5] => chunk(arr, 2) => [[1, 2], [3, 4], [5]]
export const arrayChunk = (arr, size = 1) => {
    return [...new Array(Math.ceil(arr.length / size))].fill(1)
        .map((v, i) => arr.slice(i * size, (i + 1) * size));
};



// 数字简写
export const shortNumber = (num) => {
    if (num < 1000) return num * 1;
    if (num > 1000 && num < 10000) return (num / 1000).toFixed(1) + 'k';
    if (num > 10000) return (num / 10000).toFixed(1) + 'w';
};





