import { h, Component } from 'preact';
import styled from 'styled-components';

import * as musicAction from '../redux/actions/music';
import * as videoAction from '../redux/actions/video';

import NavigationBar from '../components/NavigationBar';
import VideoBox from '../components/views/video/Box';
import Loading from '../components/Loading';
import UserInfo from '../components/views/goods/UserInfo';

const Root = styled.div`
    padding: 0;
`;

const Title = styled.h3`
    font-size: 20px;
    margin: 0;
    font-weight: bold;
    color: #222;
    padding: 12px;
    background-color: #fff;
`;

const Describe = styled.pre`
    color: #222;
    font-size: 16px;
    padding: 12px;
    background-color: #fff;
    margin: 0;
    margin-top: 12px;
`;

export default class VideoDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            play: false,
            status: 1,
            id: this.props.id,
            title: '',
            url: '',
            thumb: '',
            time: '',
            summary: '',
            describe: '',
            author: '',
            avatar: '',
        };
    }
    componentDidMount() {
        videoAction.detail(this.state.id, this.setState.bind(this));
    }
    render() {
        const { play, status, title, describe } = this.state;

        return (
            <Root>
                {/* <Loading show={status === 1} /> */}
                <NavigationBar title='视频' />
                <UserInfo {...this.state} />
                <VideoBox {...this.state} play={play} showUser={false}
                    onClick={e => {
                        musicAction.updatePlayStatus(false);
                        this.setState({ play: !play });
                    }}
                    style={{ height: 220 }}
                />
                <Title>{title}</Title>
                <Describe>{describe}</Describe>
            </Root>
        );
    }
}


