import { commit } from '../util';
import * as api from '../../api';

export const init = () => {
    api.appInfo().then(res => {
        if (!res) return;
        commit('index', state => ({ ...res }));
    });
};

// 更新 tabs 的激活索引
export const updateTabsVideIndex = index => {
    commit('index', state => ({ index }));
    window.scrollTo(0, 0);
};

// 更新路由的 url 到 redux 里
export const updateRouerUrl = url => {
    commit('index', state => ({ url }));
};

// 关闭/打开图片弹框
export const showImageModal = (flag = true, data = []) => {
    commit('index', state => ({ imageShow: flag, imageData: data }));
};


