import { h } from 'preact';
import styled from 'styled-components';

import ImageView from './ImageView';
import VideoView from './VideoView';
import GoodsView from './GoodsView';
import MusicView from './MusicView';

const Root = styled.div`
    background-color: #fff;
`;

const Content = styled.pre`
    color: #444;
    padding: 10px 5px;
    margin: 0;
    margin-top: 6px;
`;

export default ({ content = '', type, typeData, grow = false, onClick, onTypeDataClick }) => {

    return (
        <Root>
            {content && content.length > 0 &&
                <Content onClick={onClick}>{content.length > 80 ?
                    content.slice(0, 80) + '...' : content
                }</Content>
            }
            {({
                image: <ImageView data={typeData} grow={grow} onClick={onTypeDataClick} />,
                music: typeData && <MusicView data={typeData} onClick={onTypeDataClick} />,
                video: typeData && <VideoView data={typeData} onClick={onTypeDataClick} />,
                goods: typeData && <GoodsView data={typeData} onClick={onTypeDataClick} />,
            })[type] || null}
        </Root>
    );
};


