import { h } from 'preact';
import styled from 'styled-components';
import Qrc from 'qrcode.react';

const LogoIcon = require('../assets/logo_168.png');
const WxQrcodeIcon = require('../assets/qrcode-wechat.jpg');

import route from '../route';

const Root = styled.div`
    background-color: #fff;
    padding: 25px 30px;
    user-select: none;
`;

const Row = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`;

export const Logo = styled.img`
    height: 50px;
    width: 50px;
    border-radius: 100%;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
`;

export const Title = styled.span`
    margin-left: 8px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
`;

export const Btn = styled.div`
    padding: 10px 16px;
    border-radius: 25px;
    width: 90%;
    margin: 0 auto;
    background-color: rgba(1, 1, 1, 0.24);
    font-size: 14px;
    color: #333;
    text-align: center;
    margin-top: 16px;
    box-shadow: 1px 2px 3px rgba(1, 1, 1, 0.12);
    background-color: rgba(1, 1, 1, 0.24);

    > a {
        color: #333 !important;
    }
`;

export const Copyright = styled.div`
    font-size: 12px;
    color: #444;
    margin-top: 8px;
    text-align: center;
`;

export const Qrcode = styled.div`
    height: 120px;
    width: 90px;
    border-radius: 3px;
    overflow: hidden;
    background-color: rgba(1, 1, 1, 0.24);
    text-align: center;
    padding: 5px;
    > span {
        color: #fff;
        font-size: 13px;
    }
`;

export const Footer = styled.div`
    margin: 16px auto 6px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 220px;
`;

export default () => (
    <Root className='gradient'>
        <Row>
            <Logo src={LogoIcon} />
            <Title>中山原创音乐</Title>
        </Row>
        <Btn className='gradient-dark' onClick={route.download}>下载 App，查看更多原创内容</Btn>
        <Copyright>中山原创音乐协会 © {new Date().getFullYear()} 版权所有</Copyright>

        <Footer>
            <Qrcode className='gradient-dark'>
                <Qrc value="daksdlas打死你打打到那时达拉斯大数类打死你打kdl"
                    size={80}
                />
                <span>扫码下载 App</span>
            </Qrcode>
            <Qrcode className='gradient-dark'>
                <img
                    src={WxQrcodeIcon}
                    style={{ width: 80, height: 80 }}
                />
                <span>关注公众号</span>
            </Qrcode>
        </Footer>
    </Root>
);




