import { h } from 'preact';
import { connect } from 'react-redux';

import * as indexAction from '../redux/actions/index';
import * as activityAction from '../redux/actions/activity';
import route from '../route';

import ShallowEqualComponent from '../components/ShallowEqualComponent';
import Item from '../components/views/activity/Item';
import Topbar from '../components/TopBar';
import FooterBar from '../components/FooterBar';

export default connect(
    state => ({ state: state.activity }),
)(class extends ShallowEqualComponent {

    componentDidMount() {
        activityAction.init();
    }

    render({ state }) {
        return (
            <div>
                <Topbar />
                {state.list.map((item, index) => (
                    <Item key={index}
                        {...item}
                        grow={false}
                        onClick={e => {
                            route.activityDetail(item.id);
                        }}
                        onTypeDataClick={e => {
                            if (item.type === 'image') {
                                indexAction.showImageModal(true, item.typeData);
                                return;
                            }
                            try {
                                route[`${item.type}Detail`](item.typeData.id);
                            }
                            catch (err) { }
                        }}
                    />
                ))}

                <FooterBar />
            </div>
        );
    }
});




