import { h } from 'preact';
import styled from 'styled-components';

export default styled.div`
    position: absolute;
    top: 5px;
    left: 5px;
    background-color: rgba(255, 30, 30, 0.36);
    color: #fff;
    font-size: 12px;
    padding: 2px 4px 1px;
    border-radius: 3px;
    text-shadow: 1px 2px 2px rgba(1, 1, 1, 0.24);
    box-shadow: 1px 2px 3px rgba(1, 1, 1, 0.12);
`;


