import { h } from 'preact';
import { connect } from 'react-redux';
import styled from 'styled-components';

import route from '../route';
import * as goodsAction from '../redux/actions/goods';

import ShallowEqualComponent from '../components/ShallowEqualComponent';
import GoodsBox from '../components/views/goods/box';
import { arrayChunk } from '../functions';
import Topbar from '../components/TopBar';
import FooterBar from '../components/FooterBar';

const Root = styled.div`
    display: flex;
    flex-direction: column;
`;

const Row = styled.div`
    display: flex;
`;

export default connect(
    state => ({ state: state.goods }),
)(class extends ShallowEqualComponent {

    componentDidMount() {
        goodsAction.init();
    }

    render({ state }) {
        return (
            <Root>
                <Topbar />
                {arrayChunk(state.list, 2).map((v, i) => (
                    <Row>
                        {v.map((item, index) => (
                            <GoodsBox key={index}
                                {...item}
                                onClick={e => route.goodsDetail(item.id)}
                            />
                        ))}
                    </Row>
                ))}
                <div style={{ height: 12 }}></div>
                <FooterBar />
            </Root>
        );
    }
});



