import { commit } from '../util';
import * as api from '../../api';
import storage from '../../storage';

// 初始化
export const init = () => {
    api.ad().then(res => {
        if (!res) return;
        commit('home', state => ({ ad: res }));
        storage.set('HOME-AD', res);
    });
    api.albumList().then(res => {
        if (!res) return;
        commit('home', state => ({ albumList: res.list }));
        storage.set('HOME-ALBUMLIST', res.list);
    });
    api.musicList(1, 12, true).then(res => {
        if (!res) return;
        commit('home', state => ({ musicList: res.list }));
        storage.set('HOME-MUSICLIST', res.list);
    });
};

// album detail
export const albumDetail = (id, update) => {
    api.albumDetail(id).then(res => {
        if (!res) return;
        update({ ...res, status: 2 });
    });
};


