import { h } from 'preact';
import styled from 'styled-components';

import Swiper from './Swiper';

const Root = styled.div`
    height: 220px;
    width: 100%;
    background-color: #f4f4f4;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
`;

const Thumb = styled.div`
    background: url("${p => p.image}") no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
`;

const Tag = styled.div`
    padding: 1px 2px;
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: #fff;
    background-color: rgba(255, 30, 30, 0.32);
    border-radius: 5px;
    font-size: 12px;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
`;
// data = [{ thumb }]
export default ({ data = [] }) => (
    <Root>
        <Swiper id='ad' options={{
            initialSlide: 0,
            autoplay: {
                delay: 5000,
                stopOnLastSlide: false,
                disableOnInteraction: true,
            },
            pagination: {
                el: '.swiper-pagination',
            },
        }} style={{ height: 220 }}>
            {data.map((item, index) => (
                <div style={{ height: 220, width: '100%', overflow: 'hidden' }}>
                    <Thumb image={item.thumb} />,
                    <Tag>广告</Tag>
                </div>
            ))}
        </Swiper>
    </Root>
);


