import { h, Component } from 'preact';
import styled from 'styled-components';

import route from '../route';
import * as indexAction from '../redux/actions/index';
import * as activityAction from '../redux/actions/activity';

import NavigationBar from '../components/NavigationBar';
import Loading from '../components/Loading';
import UserInfo from '../components/views/goods/UserInfo';
import Box from '../components/views/activity/Box';

const Panel = styled.div`
    margin-top: 6px;
    padding: 3px 12px 12px;
    background-color: #fff;
`;

export default class ActivityDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: this.props.id,
            avatar: '',
            author: '',
            level: '',
            content: '',
            summary: '',
            time: '',
            type: 'image',
            typeData: [],
            status: 1,
        };
    }
    componentDidMount() {
        activityAction.detail(this.state.id, this.setState.bind(this));
    }

    render() {
        const { status, type, typeData } = this.state;

        return (
            <div>
                {/* <Loading show={status === 1} /> */}
                <NavigationBar title='动态' />
                <UserInfo {...this.state} isPublish={false} />
                <Panel>
                    <Box {...this.state} grow={!false}
                        onTypeDataClick={e => {
                            if (type === 'image') {
                                indexAction.showImageModal(true, typeData);
                                return;
                            }
                            try {
                                route[`${type}Detail`](typeData.id);
                            }
                            catch (err) { }
                        }}
                    />
                </Panel>
            </div>
        );
    }
}


