import { h } from 'preact';
import styled from 'styled-components';

import { arrayChunk } from '../../../functions';
import Tag from './Tag';

const W = () => Math.min(window.innerWidth, 640) / 3 - 16;

const Image = styled.div`
    width: ${W()}px;
    height: ${W()}px;
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    border-radius: 3px;
    margin: 4px;
    background-color: #f4f4f4;
`;

const Root = styled.div`
    display: flex;
`;

const Row = styled.div`
    display: flex;
`;

const Panel = styled.div`
    position: relative;
    /* background-color: #f4f4f4; */
    display: flex;
    align-items: center;
    border-radius: 3px;
`;

export default ({ data = [], grow = false, onClick }) => (
    <Root onClick={onClick}>
        {grow ?
            <div>
                {arrayChunk(data, 3).map((v, i) => (
                    <Row key={i}>
                        {v.map((item, index) => (
                            <Image src={item} key={index} />
                        ))}
                    </Row>
                ))}
            </div> :
            <Panel>
                {data.slice(0, 3).map((item, index) => (
                    <Image src={item} key={index} />
                ))}
                <Tag>{data.length} 张</Tag>
            </Panel>
        }
    </Root>
);


