import { commit } from '../util';
import * as api from '../../api';
import storage from '../../storage';

// 初始化
export const init = () => {
    api.activityList().then(res => {
        if (!res) return;
        // console.log(res);
        commit('activity', state => ({ ...res }));
        storage.set('ACTIVITY-LIST', res.list);
    });
};

export const detail = (id, update) => {
    api.activityDetail(id).then(res => {
        if (!res) return;
        update({ ...res, status: 2 });
    });
};


