import isFunction from 'is-function';
import storage from '../storage';

function createReducer(name, initState) {
    return (state = initState, action) => {
        if (action.newState && !isFunction(action.newState)) {
            console.warn(`[${name}] action.newState is not a function`);
            return state;
        }
        if (action.type === name) {
            return action.newState(state, initState) || state;
        }
        return state;
    };
}

// 主题
export const theme = createReducer('theme', {
    color: '#39f',
    navBarHeight: 55,
    tabsViewHeight: 55,
    mainMaxWidth: 640,
});

