    imageShow: false, // 是否打开（会全屏哦）

    // app info
    appVersion: 'v1.0.0',
    appUrl: '',
    appName: '中山原创音乐',
    appSummary: '中山原创音乐 App 以原创音乐人服务为核心，提供一个自主的系统工具，展示、交流和推广原创音乐作品，提高音乐作品变现能力，力求打造原创音乐人的全方位展示平台！',
