import { Component } from 'preact';
import shallowEql from 'shallowequal';

export default class shallowEqualComponent extends Component {
    shouldComponentUpdate(nextProps, nextState) {
        return (
            !shallowEql(nextProps, this.props) ||
            !shallowEql(nextState, this.state)
        );
    }
}



