import { h, Component } from 'preact';
import styled from 'styled-components';

import * as goodsAction from '../redux/actions/goods';

import NavigationBar from '../components/NavigationBar';
import Swiper from '../components/Swiper';
import Loading from '../components/Loading';
import UserInfo from '../components/views/goods/UserInfo';


const Root = styled.div`
    position: relative;
`;

const Thumb = styled.div`
    background: url("${p => p.image}") center center no-repeat;
    background-size: cover;
    height: 100%;
    width: 100%;
`;

const Header = styled.div`
    background-color: #fff;
    padding: 16px;

    span {
        color: #999;
        font-size: 12px;
    }
`;

const Title = styled.div`
    font-size: 20px;
    font-weight: bold;
`;

const Describe = styled.pre`
    padding: 12px;
    background-color: #fff;
    margin: 0;
    margin-top: 12px;
    color: #555;
    width: 100%;
`;

const Row = styled.div`
    display: flex;
    justify-content: space-between;
`;

const Num = styled.div`
    font-size: 17px;
    color: #f45;
    font-weight: bold;
    margin-bottom: 6px;
`;

export default class GoodsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // 状态
            status: 1,
            //
            id: this.props.id,
            price: 0,
            title: '',
            avatar: '',
            author: '',
            time: '',
            photos: [],
            describe: '',
            summary: '',
            inventory: 0,
            payNum: 0,
            freight: 0,
        };
    }
    componentDidMount() {
        goodsAction.detail(this.state.id, this.setState.bind(this));
    }
    render() {
        const {
            photos, title, price, summary, describe,
            author, avatar, time, freight, inventory,
            payNum, status,
        } = this.state;

        return (
            <Root>
                {/* <Loading show={status === 1} /> */}
                <NavigationBar title='商品' />
                <UserInfo
                    avatar={avatar}
                    author={author}
                    summary={summary}
                    time={time}
                />
                <Swiper id='goods' options={{
                    initialSlide: 0,
                    autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                        disableOnInteraction: true,
                    },
                    pagination: {
                        el: '#pagination-goods',
                    },
                }} style={{ height: 240 }}>
                    {photos.map((item, index) => (
                        <Thumb image={item} key={index} />
                    ))}
                </Swiper>
                <Header>
                    <Title>{title}</Title>
                    <Num>¥ {price}</Num>
                    <Row>
                        <span>{freight * 1 === 0 ? '包邮' : freight}</span>
                        <span>库存 {inventory} 件</span>
                        <span>{payNum} 人付款</span>
                    </Row>
                </Header>

                <Describe>{describe}</Describe>
            </Root>
        );
    }
}


