import { h } from 'preact';
import styled from 'styled-components';

import Tag from './Tag';

const PlayIcon = require('../../../assets/play.svg');

const Root = styled.div`
    position: relative;
    height: 180px;
    width: 90%;
    margin: 12px auto;
`;

const Thumb = styled.div`
    background: url("${p => p.src}") center center no-repeat;
    background-size: cover;
    height: 150px;
    width: 100%;
    border-radius: 3px 3px 0 0;
`;

const Title = styled.div`
    background-color: #f4f4f4;
    padding: 6px 12px;
`;

const Play = styled.img`
    position: absolute;
    bottom: 50px;
    right: 20px;
    width: 40px;
    height: 40px;
    background-color: rgba(1, 1, 1, 0.24);
    border-radius: 100%;
`;

export default ({ data: { title, thumb }, onClick }) => (
    <Root onClick={onClick}>
        <Thumb src={thumb} />
        <Title className='space'>{title}</Title>
        <Play src={PlayIcon} />
        <Tag>视频</Tag>
    </Root>
);


