import { h } from 'preact';
import { connect } from 'react-redux';

import route from '../route';
import * as musicAction from '../redux/actions/music';
import * as videoAction from '../redux/actions/video';

import ShallowEqualComponent from '../components/ShallowEqualComponent';
import Item from '../components/views/video/Item';
import Topbar from '../components/TopBar';
import FooterBar from '../components/FooterBar';

export default connect(
    state => ({ state: state.video }),
)(class extends ShallowEqualComponent {

    componentDidMount() {
        videoAction.init();
    }

    render({ state }) {
        return (
            <div>
                <Topbar />
                {state.list.map((v, i) => (
                    <Item key={i} {...v}
                        play={i === state.index}
                        onInfo={e => {
                            route.videoDetail(v.id);
                            videoAction.updatePlayIndex(-1);
                        }}
                        onPlay={e => {
                            musicAction.updatePlayStatus(false);
                            videoAction.updatePlayIndex(i);
                        }}
                    />
                ))}

                <FooterBar />
            </div>
        );
    }
});


