import { h, Component } from 'preact';
import styled from 'styled-components';
import { connect } from 'react-redux';

import * as musicAction from '../redux/actions/music';
import route from '../route';

import Loading from '../components/Loading';
import TimeText from '../components/views/music/TimeText';
import Avatar from '../components/Avatar';
import Icon from '../components/Icon';

const PlayIcon = require('../assets/play_2.svg');
const PauseIcon = require('../assets/pause.svg');

const Root = styled.div`
    background-color: #fff;
    height: 100%;
    height: 100vh;
    background: url("${p => p.image}") center center no-repeat;
    background-size: cover;
    box-shadow: 0 0 120px rgba(1, 1, 1, 0.54) inset;
    text-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
`;

const Thumb = styled.div`
    display: flex;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    background: url("${p => p.image}") center center no-repeat;
    background-size: cover;
    box-shadow:
        1px 3px 20px rgba(1, 1, 1, 0.08),
        0 0 80px rgba(1, 1, 1, 0.8) inset;
    justify-content: center;
    align-items: center;
    text-shadow: 1px 2px 2px rgba(1, 1, 1, 0.24);
    background-color: #fff;

    animation: music-thumb 60s infinite linear;
    ${p => !p.play && `
        animation-play-state: paused;
    `}
`;

const Play = styled.img`
    height: 40px;
    width: 40px;
    border-radius: 100%;
    background-color: rgba(1, 1, 1, 0.24);
`;

const Title = styled.div`
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    margin: 20px 0 2px;
`;

const Content = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    max-width: ${p => p.theme.mainMaxWidth}px;
    margin: 0 auto;
    background-color: rgba(1, 1, 1, 0.54);
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
`;

const Time = styled.div`
    position: absolute;
    bottom: 8px;
    left: 0; right: 0;
`;

const Close = styled.div`
    position: absolute;
    top: 20px;
    left: 15px;
`;

export default connect(
    state => ({ state: state.music }),
)(class MusicDetail extends Component {

    componentDidMount() {
        const id = this.props.id;
        if (this.props.state.song.id !== id && id * 1 !== -1) {
            musicAction.detail(id);
        }
    }

    render({ state }) {
        const { song, play, status, progressData } = state;

        return (
            <Root image={song.thumb}>
                {/* <Loading show={status === 1} /> */}
                <Content>
                    <div style={{ position: 'relative' }}>
                        <Thumb
                            image={song.thumb}
                            play={play}
                            onClick={e => musicAction.updatePlayStatus()}>
                            <Play src={!play ? PlayIcon : PauseIcon} />
                        </Thumb>
                        <Time>
                            <TimeText
                                start={progressData.currentTime || 0}
                                end={progressData.duration || 0}
                            />
                        </Time>
                    </div>
                    <Title>{song.title}</Title>
                    <Avatar url={song.avatar} size={22}>
                        <span style={{ marginLeft: 5, color: '#fff' }}>
                            {song.author}
                        </span>
                    </Avatar>

                    <Close onClick={e => route.go(-1)}>
                        <Icon name='close' color='rgba(255, 255, 255, 0.85)' size={26} />
                    </Close>
                </Content>
            </Root>
        );
    }
});




