import { commit } from '../util';
import * as api from '../../api';
import storage from '../../storage';

export const init = () => {
    api.goodsList().then(res => {
        if (!res) return;
        commit('goods', state => ({ ...res }));
        storage.set('GOODS-LIST', res.list);
    });
};

export const detail = (id, update) => {
    api.goodsDetail(id).then(res => {
        if (!res) return;
        update({ ...res, status: 2 });
    });
};

// 加载更多
export const loadmore = () => {

};



