import { h } from 'preact';
import TabsBar from '../components/TabsBar';
import styled from 'styled-components';
import { connect } from 'react-redux';

import route from '../route';
import * as indexAction from '../redux/actions/index';

import ShallowEqualComponent from '../components/ShallowEqualComponent';
import Home from './Home';
import Video from './Video';
import Activity from './Activity';
import Mall from './Mall';

const Root = styled.div`
    padding-bottom: ${p => p.theme.tabsViewHeight}px;
`;

const TabView = styled.div`
    display: none;

    ${p => p.active && `
        display: block;
    `}
`;

export default connect(
    state => ({ state: state.index, music: state.music }),
)(class Index extends ShallowEqualComponent {

    componentDidMount() {
        indexAction.init();
    }

    render({ state, music }) {
        const { index, url } = state;
        const { song, play } = music;

        return (
            <Root>
                <TabView active={index === 0}>
                    <Home />
                </TabView>
                <TabView active={index === 1}>
                    <Activity />
                </TabView>
                <TabView active={index === 2}>
                    <Video />
                </TabView>
                <TabView active={index === 3}>
                    <Mall />
                </TabView>

                <TabsBar
                    selectedRoute={url}
                    index={index}
                    onTab={indexAction.updateTabsVideIndex}
                    image={song.thumb}
                    onMini={e => song.id !== undefined && route.musicDetail(song.id)}
                    play={play}
                />
            </Root>
        );
    }
});




