import { h } from 'preact';
import { Router } from 'preact-router';
import createHashHistory from 'history/createHashHistory';
import { connect } from 'react-redux';
import Audio from 'react-imusic';

import * as action from './redux/actions/index';
import * as musicAction from './redux/actions/music';

import NotFound from './views/404';
import ThemeContainer from './container/Theme';

import Index from './views/Index';
import MusicDetail from './views/Music-Detail';
import VideoDetail from './views/Video-Detail';
import GoodsDetail from './views/Goods-Detail';
import ActivityDetail from './views/Activity-Detail';
import AlbumDetail from './views/Album-Detail';
import DownLoad from './views/DownLoad';

import ImageModal from './components/ImageModal';

const App = connect(
    state => ({ music: state.music, index: state.index }),
)(({ music, index }) => {
    const { song, play } = music;

    return (
        <div id='app'>
            {/* 全局的存在 */}
            <Audio
                ref={r => this.audio = r}
                src={song.url}
                play={play}
                onLoad={d => musicAction.updateProgressData(d)}
                onProgress={d => musicAction.updateProgressData(d)}
                onError={e => musicAction.updatePlayStatus(false)}
                onEnd={e => musicAction.updatePlayStatus(false)}
            />

            <Router
                onChange={e => action.updateRouerUrl(e.url)}
                history={createHashHistory()}>
                <Index path='/' />

                <MusicDetail path='/music-detail/:id' />
                <VideoDetail path='/video-detail/:id' />
                <GoodsDetail path='/goods-detail/:id' />
                <AlbumDetail path='/album-detail/:id' />
                <ActivityDetail path='/activity-detail/:id' />
                <DownLoad path='/download' />

                <NotFound default />
            </Router>

            {/* 全局用于查看图片的 Modal */}
                        <ImageModal
                show={index.imageShow}
                data={index.imageData}
                onClose={e => action.showImageModal(false)}
            />
        </div>
    );
});

export default () => (
    <ThemeContainer>
        <App />
    </ThemeContainer>
);


