import { h } from 'preact';
import styled from 'styled-components';

const LogoIcon = require('../assets/logo_168.png');

const Root = styled.div`
    height: 60px;
    box-shadow: 1px 2px 10px rgba(1, 1, 1, 0.12);
    display: flex;
    align-items: center;
    padding: 6px 12px;
    user-select: none;
`;

const Logo = styled.img`
    height: 40px;
    width: 40px;
    border-radius: 100%;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
`;

const Title = styled.div`
    font-size: 16px;
    color: #333;
    font-weight: bold;
    padding: 1px 12px 0;
    flex: 1;
    line-height: 1;
`;

const Btn = styled.div`
    color: #333;
    font-size: 13px;
    background-color: rgba(1, 1, 1, 0.24);
    border-radius: 5px;
    padding: 10px 16px;
    box-shadow: 1px 2px 2px rgba(1, 1, 1, 0.12);
    text-align: center;
    line-height: 1;
    font-weight: bold;
`;

export default () => (
    <Root className='gradient'>
        <Logo src={LogoIcon} />
        <Title>中山原创音乐</Title>
        <Btn className='gradient-dark'>App 下载</Btn>
    </Root>
);


