
const p = {
    // 解密
    decode(str) {
        str = str + '';
        str = str.split('.');
        let s = [];
        for (let i = 0; i < str.length; i++) {
            s.push(String.fromCharCode(str[i]));
        }
        return s.join('');
        // return str;
    },
    // 加密
    encode(str) {
        str = str + '';
        let s = [];
        for (let i = 0; i < str.length; i++) {
            s.push(str[i].charCodeAt(0));
        }
        return s.join('.');
        // return str;
    },
};

export default {
    get(key) {
        const str = p.decode(localStorage.getItem(key));
        try {
            if (!str) return;
            const data = JSON.parse(str);
            return data;
        }
        catch (err) {
            // console.error(err);
        }
    },
    set(key, value) {
        try {
            const str = JSON.stringify(value);
            if (!str) return;
            const val = p.encode(str);
            localStorage.setItem(key, val);
        }
        catch (err) {
            // console.error('set', err);
        }
    }
};


